import {Injectable} from '@angular/core';
import { SellerUrlBase } from '../seller-url-base';
@Injectable({
	providedIn: 'root'
})
export class SellerUrlStatistic extends SellerUrlBase {
	readonly adminUrlStatistic: string = this.sellerUrl + '/statistic';
	getStatistic(): string {
		return this.adminUrlStatistic;
	}
}
