import {SpinnerService} from './../../../../../../core/services/comon-services/spinner/spinner.service';
import {AppErrorHandler} from './../../../../../../core/error-handler/app-error-handler.service';
import {Component, OnInit} from '@angular/core';
import {takeUntil} from 'rxjs/operators';
import {PagedResult} from 'src/app/core/models/common/page/PagedResult';
import {SellerAllProductVM} from 'src/app/core/models/seller/product/seller-all-product.viewmodel';
import {NotificationService} from 'src/app/core/notification/notifications/customer/notification.service';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {SellerProductService} from 'src/app/core/services/seller/seller-product/seller-product.service';
import {environment} from 'src/environments/environment';

@Component({
	selector: 'app-product-all',
	templateUrl: './product-all.component.html',
	styleUrls: ['./product-all.component.less'],
	providers: [DestroyService]
})
export class ProductAllComponent implements OnInit {
	loading = false;
	startOfIndex = 1;
	tabIndex = 0;
	host: string = environment.apiUrl;
	pageResult: PagedResult<SellerAllProductVM>;
	listOfData: Array<SellerAllProductVM> = [];

	constructor(
		private destroy$: DestroyService,
		private sellerProductService: SellerProductService,
		private notificationService: NotificationService,
		private appErrorHandler: AppErrorHandler,
		private spinner: SpinnerService
	) {
		this.pageResult = {} as PagedResult<SellerAllProductVM>;
	}
	ngOnInit(): void {
		this.getProducts(this.startOfIndex, this.tabIndex);
	}
	getProducts(pageIndex: number, typeProduct: number) {
		this.loading = true;
		this.sellerProductService
			.getAllProductPaging(pageIndex, typeProduct)
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					if (result.resultObj) {
						this.pageResult = result.resultObj;
						Array.prototype.push.apply(this.listOfData, result.resultObj.items);
						this.loading = false;
						console.log(this.listOfData);
					}
					this.loading = false;
				},
				(error) => {
					this.loading = false;
					this.notificationService.showErrorNotification(error.message);
				}
			);
	}
	onScroll() {
		this.startOfIndex = this.startOfIndex + 1;
		console.log(this.startOfIndex);
		this.getProducts(this.startOfIndex, this.tabIndex);
	}
	changeTab(tabIndex: number) {
		this.tabIndex = tabIndex;
		this.startOfIndex = 1;
		this.loading = true;
		this.pageResult = {} as PagedResult<SellerAllProductVM>;
		this.listOfData = [];
		this.getProducts(this.startOfIndex, this.tabIndex);
	}
	confirmDeleteProduct(productId: number) {
		this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: true});
		console.log(productId);
		this.sellerProductService
			.deleteProduct(productId)
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					if (result) {
						if (result.isSuccessed) {
							this.listOfData = [];
							this.getProducts(1, this.tabIndex);
							if (result.message) {
								this.notificationService.showSuccessNotification(result.message);
							}
						} else {
							if (result.message) {
								this.notificationService.showSuccessNotification(result.message);
							}
						}
					}
					this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
				},
				(error) => {
					this.appErrorHandler.handleErrorWithNoti(error);
				}
			);
	}
	confirmLockProduct(productId: number) {
		this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: true});
		console.log(productId);
		this.sellerProductService
			.lockProduct(productId)
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					if (result) {
						if (result.isSuccessed) {
							this.listOfData = [];
							this.getProducts(1, this.tabIndex);
							if (result.message) {
								this.notificationService.showSuccessNotification(result.message);
							}
						} else {
							if (result.message) {
								this.notificationService.showErrorNotification(result.message);
							}
						}
					}
					this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted: false});
				},
				(error) => {
					this.appErrorHandler.handleErrorWithNoti(error);
				}
			);
	}
}
