import {createFeatureSelector, createSelector} from '@ngrx/store';
import {SellerAppState} from '../seller-app.state';
import {SellerSettingsState} from './seller-settings.state';


export const selectSettingsState = createFeatureSelector<SellerAppState, SellerSettingsState>('sellersettings');
export const selectSettings = createSelector(selectSettingsState, (state: SellerSettingsState) => state);
export const selectTheme = createSelector(selectSettings, (settings) => settings.theme);
export const selectSettingsLanguage = createSelector(selectSettings, (state: SellerSettingsState) => state.language);
