import {Observable} from 'rxjs';
import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {ApiResult} from 'src/app/core/models/common/ApiResult';
import {NotificationCountResponse} from 'src/app/core/models/customer/notification/response/notification-count-response';
import {PagingRequestBase} from 'src/app/core/models/common/page/pagingrequest/PagingRequestBase';
import {NotificationResponse} from 'src/app/core/models/common/notification/response/notification-response.viewmodel';
import {PagedResult} from 'src/app/core/models/common/page/PagedResult';
import {SellerUrlNotification} from 'src/app/core/utils/URL/seller-url/seller-notification/seller-url-notification';
@Injectable({
	providedIn: 'root'
})
export class SellerNotificationService {
	constructor(private http: HttpClient, private sellerUrlNotification: SellerUrlNotification) {}

	getNotificationCount(): Observable<ApiResult<NotificationCountResponse>> {
		console.log(this.sellerUrlNotification.sellerGetNotificationCount());
		return this.http.get<ApiResult<NotificationCountResponse>>(
			this.sellerUrlNotification.sellerGetNotificationCount()
		);
	}
	getNotificationList(request: PagingRequestBase) {
		return this.http.get<ApiResult<PagedResult<NotificationResponse>>>(
			this.sellerUrlNotification.sellerGetNotificationList(request)
		);
	}
	postNotificationHasRead(listHasRead: Array<number>) {
		return this.http.post(this.sellerUrlNotification.sellerPostNotificationHasReadList(), listHasRead);
	}
}
