import { Injectable } from '@angular/core';
import {RequestBase} from 'src/app/core/models/common/request/RequestBase';
import {SellerUrlBase} from './../seller-url-base';
@Injectable({
	providedIn: 'root'
})
export class SellerUrlAuthor extends SellerUrlBase {
	constructor() {
		super();
	}
	sellerGetAuthorListAPI(requestBase: RequestBase): string {
		return this.sellerUrl + `/author/authors/${requestBase.languageCode}`;
	}
	sellerGetAuthorBySearchKeyWordListAPI(requestBase: RequestBase): string {
		return (
			this.sellerUrl +
			`/author/authors/?languageCode=${requestBase.languageCode}&searchKeyWord=${requestBase.searchKeyWord}`
		);
	}
}
