import { Injectable } from '@angular/core';
import {RequestBase} from 'src/app/core/models/common/request/RequestBase';
import {SellerUrlBase} from '../seller-url-base';
@Injectable({
	providedIn: 'root'
})
export class SellerUrlCategory extends SellerUrlBase {
	constructor() {
		super();
	}
	getCategoryVariationByCatgoryIdAPI(requestBase: RequestBase, categoryId: number): string {
		return this.sellerUrl + `/category/${categoryId}/categoryvariations/${requestBase.languageCode}`;
	}
	getCategoryVariationByCatgoryIdBySearchKeyWordAPI(requestBase: RequestBase, categoryId: number): string {
		return (
			this.sellerUrl +
			`/category/${categoryId}/categoryvariations/?languageCode=${requestBase.languageCode}&searchKeyWord=${requestBase.searchKeyWord}`
		);
	}
}
