import { Injectable } from '@angular/core';
import { RequestBase } from 'src/app/core/models/common/request/RequestBase';
import {SellerUrlBase} from './../seller-url-base';
@Injectable({
	providedIn: 'root'
})
export class SellerUrlPublisher extends SellerUrlBase {
	constructor() {
		super();
	}

	sellerGetPublisherListAPI(requestBase: RequestBase): string {
		return this.sellerUrl + `/publisher/publishers/${requestBase.languageCode}`;
	}

	sellerGetPublisherBySearchKeyWordListAPI(requestBase: RequestBase): string {
		return (
			this.sellerUrl +
			`/publisher/publishers/?languageCode=${requestBase.languageCode}&searchKeyWord=${requestBase.searchKeyWord}`
		);
	}
}
