import { SellerUrlBase } from './../seller-url-base';
import {HttpParams} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {PagingRequestBase} from 'src/app/core/models/common/page/pagingrequest/PagingRequestBase';
@Injectable({
	providedIn: 'root'
})
export class SellerUrlNotification extends SellerUrlBase {
	constructor() {
		super();
	}
	sellerGetNotification() {
		return this.sellerUrl + '/chat/notification';
	}
	sellerGetNotificationCount() {
		return this.sellerUrl + '/notification/notification-count';
	}
	sellerGetNotificationList(request: PagingRequestBase) {
		let params = new HttpParams()
			.set('pageindex', request.pageIndex == undefined ? '1' : request.pageIndex.toString())
			.set('pagesize', request.pageSize == undefined ? '10' : request.pageSize.toString());

		return this.sellerUrl + '/notification?' + params.toString();
	}
	sellerPostNotificationHasReadList(): string {
		return this.sellerUrl + '/notification';
	}
}
