import { FinanceModule } from './components/seller-body/module/finance/finance.module';
import {ProductUpdateComponent} from './components/seller-body/product/product-update/product-update.component';
import {ProductAllComponent} from './components/seller-body/product/product-all/product-all.component';
import {ProductCreateComponent} from './components/seller-body/product/product-create/product-create.component';
import {SellerComponent} from './seller.component';
import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {HomeComponent} from './components/seller-body/home/home.component';
import {SellerAuthGuard} from 'src/app/core/guards/seller/seller-auth.guard';
import {ProductComponent} from './components/seller-body/product/product.component';

const routes: Routes = [
	{
		path: '',
		component: SellerComponent,
		canActivateChild: [SellerAuthGuard],
		children: [
			{path: '', component: HomeComponent, data: {title: 'Seller-app.content.body.home.title'}},
			{
				path: 'order',
				loadChildren: () =>
					import('../seller/components/seller-body/module/seller-order/seller-order.module').then(
						(m) => m.SellerOrderModule
					)
			},
			{
				path: 'shop',
				loadChildren: () =>
					import('../seller/components/seller-body/module/shop/shop.module').then((m) => m.ShopModule)
			},
			{
				path: 'finance',
				loadChildren: () =>
					import('../seller/components/seller-body/module/finance/finance.module').then((m) => m.FinanceModule)
			},
			{
				path: 'product',
				component: ProductComponent,
				data: {
					//breadcrumb: "Seller-app.content.body.product.product-all.breadcrumb"
					breadcrumb: 'Products'
				},
				children: [
					{
						path: '',
						component: ProductAllComponent,
						data: {
							title: 'Seller-app.content.body.product.product-all.title'
							// main_title: "Seller-app.content.body.product.product-all.main-title",
							//sub_title: "Seller-app.content.body.product.product-all.sub-title"
						}
					},
					{
						path: 'create',
						component: ProductCreateComponent,
						data: {title: 'Seller-app.content.body.product.product-create.title', breadcrumb: 'create'}
					},
					{
						path: ':productId/update',
						component: ProductUpdateComponent,
						data: {title: 'Seller-app.content.body.product.product-create.title', breadcrumb: 'update'}
					}
				]
			}
			//	{path: "create", component: ProductCreateComponent}
		]
	},

	{
		path: 'login',
		loadChildren: () => import('./module/seller-login/seller-login.module').then((m) => m.SellerLoginModule)
	},
	{
		path: 'register',
		loadChildren: () =>
			import('./module/seller-register/seller-register.module').then((m) => m.SellerRegisterModule)
	},
	{
		path: 'notfound',
		loadChildren: () =>
			import('./module/seller-notfound/seller-notfound.module').then((m) => m.SellerNotfoundModule)
	},
	{path: '**', redirectTo: 'notfound'}
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule]
})
export class SellerRoutingModule {}
