import {Injectable} from '@angular/core';
import {
	CanActivate,
	CanActivateChild,
	CanLoad,
	Route,
	UrlSegment,
	ActivatedRouteSnapshot,
	RouterStateSnapshot,
	Router
} from '@angular/router';
import {Observable} from 'rxjs';
import {AuthService} from '../../services/auth/auth.service';

@Injectable({
	providedIn: 'root'
})
export class SellerAuthGuard implements CanActivate, CanActivateChild, CanLoad {
	constructor(public router: Router, private authService: AuthService) {}

	canActivate(_next: ActivatedRouteSnapshot, _state: RouterStateSnapshot): boolean {
		return true;
	}
	canActivateChild(_next: ActivatedRouteSnapshot, _state: RouterStateSnapshot): boolean {
		if (this.authService.isAuthenticated('seller-token')) {
			//	this.router.navigate(["/seller"]);
			//	console.log('SellerAuthGuard: true');
				return true;
			} else {
				this.router.navigate(['/seller/login'],{queryParams: {returnUrl: _state.url}});
				//console.log('SellerAuthGuard: false');
				return false;
			}

	}
	canLoad(_route: Route, _segments: UrlSegment[]): Observable<boolean> | Promise<boolean> | boolean {
		return true;
	}
}
