import {catchError, exhaustMap, map} from 'rxjs/operators';
import {Actions, createEffect, ofType} from '@ngrx/effects';
import {Injectable} from '@angular/core';
import * as LoginActions from './login.actions';
import {SellerAccountService} from 'src/app/core/services/auth/seller/seller-account.service';
import {of} from 'rxjs/internal/observable/of';
import {Observable} from 'rxjs';
import {LocalStorageService} from 'src/app/core/services/comon-services/local-storage-service/localstorage.service';

@Injectable()
export class LoginEffects {
	loadSellerLogin$: Observable<any> = createEffect(() =>
		this.actions$.pipe(
			ofType(LoginActions.login),
			map((action) => action.userLogin),
			exhaustMap((userLogin) =>
				this.sellerAccountService
					.login(userLogin.email, userLogin.passWord, userLogin.remember, userLogin.urlReturn)
					.pipe(
						map(
							(result) => {
								if (result.isSuccessed && result.resultObj) {
									if (result.resultObj.tokenValue) {
										if (result.resultObj.remember) {
											this.localStorageService.removeItem('seller-token');
											this.localStorageService.setItem(
												'seller-token',
												result.resultObj.tokenValue
											);

											// this.appCookieService.remove('seller-token');
											// this.appCookieService.remove('seller-infor');
											// this.appCookieService.set(
											// 	'seller-token',
											// 	result.resultObj.tokenValue,
											// 	parseInt(
											// 		this.jwtTokenService.getExpiryTime(result.resultObj.tokenValue),
											// 		10
											// 	)
											// );
											// this.appCookieService.set(
											// 	'seller-infor',
											// 	JSON.stringify(result.resultObj),
											// 	parseInt(
											// 		this.jwtTokenService.getExpiryTime(result.resultObj.tokenValue),
											// 		10
											// 	)
											// 	// );
											// } else {
											// 	this.appCookieService.remove('seller-token');
											// 	this.appCookieService.remove('seller-infor');
											// 	this.appCookieService.set('seller-token', result.resultObj.tokenValue);
											// 	this.appCookieService.set('seller-infor', JSON.stringify(result.resultObj));
										}
										return LoginActions.loginSuccess({
											user: result.resultObj,
											message: result.message
										});
									} else {
										return LoginActions.loginFailed({message: result.message});
									}
								} else {
									return LoginActions.loginFailed({message: result.message});
								}
							},
							(error: Error) => LoginActions.loginFailed({message: error.message})
						),
						catchError((error: Error) => of(LoginActions.loginFailed({message: error.message})))
					)
			)
		)
	);
	constructor(
		private actions$: Actions,
		private sellerAccountService: SellerAccountService,
		private localStorageService: LocalStorageService
	) {}
}
