import {SpinnerService} from 'src/app/core/services/comon-services/spinner/spinner.service';
import {debounceTime, takeUntil} from 'rxjs/operators';
import {PublisherResponseVM} from './../../../../../../core/models/seller/publisher/publisher.ViewModel';
import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import * as ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import {AuthorResponseVM} from 'src/app/core/models/seller/author/authorResponse.ViewModel';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {AppErrorHandler} from 'src/app/core/error-handler/app-error-handler.service';
import {CategoryVariationResponseVM} from 'src/app/core/models/seller/category/categoryVariationResponse.ViewModel';
import {CdkDragDrop, moveItemInArray} from '@angular/cdk/drag-drop';
import {BookDetailRespond} from 'src/app/core/models/product/respond/BookDetailRespond';
import {ActivatedRoute} from '@angular/router';
import {environment} from 'src/environments/environment';
import {ProductUpdateRequest} from 'src/app/core/models/seller/product/request/ProductUpdateRequest';
import {NotificationService} from 'src/app/core/notification/notifications/customer/notification.service';
import {SellerProductService} from 'src/app/core/services/seller/seller-product/seller-product.service';
import {SellerPublisherService} from 'src/app/core/services/seller/seller-Publisher/seller-publisher.service';
import {SellerAuthorService} from 'src/app/core/services/seller/seller-author/seller-author.service';
import {SellerCategoryService} from 'src/app/core/services/seller/seller-category/seller-category.service';
@Component({
	selector: 'app-product-update',
	templateUrl: './product-update.component.html',
	styleUrls: ['./product-update.component.less', './product-update.component.css'],
	providers: [DestroyService]
})
export class ProductUpdateComponent implements OnInit {
	editor = ClassicEditor;
	submitted = false;
	uploading = false;
	readyToDisplayProductImages = false;
	isAuthorSelectLoading = false;
	isPublisherSelectLoading = false;
	isCategoryVariatonsSelectLoading = false;
	sellerCreateProductForm: FormGroup;
	authorSelectList: Array<AuthorResponseVM> = [];
	publisherSelectList: Array<PublisherResponseVM> = [];
	categoryVariationSelectList: Array<CategoryVariationResponseVM> = [];
	fileList: Array<File> = [];
	productByIdInit: BookDetailRespond;
	productId: number;
	uid = 0; ///nh danh cho image
	host: string = environment.apiUrl; //localhost:5005
	thumbshandlePreview: Array<{uid: string; productImageId?: number | null; imagePath: string}> = [];
	constructor(
		private sellerProductService: SellerProductService,
		private sellerCategoryService: SellerCategoryService,
		private sellerAuthorService: SellerAuthorService,
		private sellerPublisherService: SellerPublisherService,
		private formBuilder: FormBuilder,
		private destroy$: DestroyService,
		private appErrorHandler: AppErrorHandler,
		private route: ActivatedRoute,
		private notificationService: NotificationService,
		private spinner: SpinnerService
	) {
		this.productId = 1;
		this.productByIdInit = {} as BookDetailRespond;

		this.sellerCreateProductForm = this.formBuilder.group({
			productName: ['', [Validators.required, Validators.minLength(3)]],
			entireDescription: ['', [Validators.required, Validators.minLength(3)]],
			author: ['', [Validators.required, Validators.minLength(3)]],
			publisher: ['', [Validators.required, Validators.minLength(3)]],
			categoryVariaton: [[], [Validators.required]],
			page: ['', [Validators.required, Validators.minLength(3)]],
			publishingYear: ['', [Validators.required, Validators.minLength(3)]],
			country: ['', [Validators.required, Validators.minLength(3)]],
			price: ['', [Validators.required, Validators.minLength(3)]],
			originalPrice: ['', [Validators.required, Validators.minLength(3)]],
			stock: ['', [Validators.required, Validators.minLength(3)]],
			mass: ['', [Validators.required, Validators.minLength(3)]],
			length: ['', [Validators.required, Validators.minLength(3)]],
			width: ['', [Validators.required, Validators.minLength(3)]],
			thickness: ['', [Validators.required, Validators.minLength(3)]]
		});
	}
	ngOnInit() {
		this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted:true});
		const productIdParam = this.route.snapshot.paramMap.get('productId');
		if (productIdParam) {
			const productIdInNumber = parseInt(productIdParam, 10);
			if (!isNaN(productIdInNumber)) {
				this.productId = productIdInNumber;
				this.initFormDataFromServer();
				this.getProductById(this.productId);
			}
		}
		this.isAuthorSelectLoading = true;
		this.isPublisherSelectLoading = true;
		this.isCategoryVariatonsSelectLoading = true;
	}
	getProductById(productId: number) {
		this.sellerProductService
			.getProductById({id: productId})
			.pipe(takeUntil(this.destroy$))
			.subscribe((result) => {
				if (result) {
					if (result.isSuccessed) {
						if (result.message) {
							this.notificationService.showSuccessNotification(result.message);
						}
						if (result.resultObj) {
							this.productByIdInit = result.resultObj;

							result.resultObj.images.forEach((val) => {
								this.thumbshandlePreview.push({
									uid: (this.uid++).toString(),
									productImageId: val.productImageId,
									imagePath: val.imagepath
								});
								this.readyToDisplayProductImages = true;
							});
							this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted:false});
						}
					} else {
						if (result.message) {
							this.notificationService.showErrorNotification(result.message);
							this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted:false});
						}
					}

					console.log(result);
				}
				() => {
					this.notificationService.showErrorNotification('C Li Xy Ra, Vui Lng Th Li');
				};
			});
	}
	initFormDataFromServer() {
		setTimeout(() => {
			this.sellerCreateProductForm.setValue({
				productName: this.productByIdInit.productName,
				entireDescription: this.productByIdInit.entireDescription,
				author: this.productByIdInit.authorId,
				publisher: this.productByIdInit.publisherId,
				categoryVariaton: this.productByIdInit.categoryVariations.map(
					({categoryVariationId}) => categoryVariationId
				),
				page: this.productByIdInit.page,
				publishingYear: this.productByIdInit.datePublisher,
				country: this.productByIdInit.countryId,
				price: this.productByIdInit.price,
				originalPrice: this.productByIdInit.originalPrice,
				stock: this.productByIdInit.stock,
				mass: this.productByIdInit.mass,
				length: this.productByIdInit.length,
				width: this.productByIdInit.width,
				thickness: this.productByIdInit.thickness
			});
		}, 5000);
	}
	initAuthorList(searchKeyWord: string) {
		this.isAuthorSelectLoading = true;
		this.sellerAuthorService
			.getAuthorList(searchKeyWord)
			.pipe(takeUntil(this.destroy$))
			.subscribe((result) => {
				this.authorSelectList = result.resultObj;
				this.isAuthorSelectLoading = false;
			});
	}
	initPublisherList(searchKeyWord: string) {
		this.isPublisherSelectLoading = true;
		this.sellerPublisherService
			.getPublisherList(searchKeyWord)
			.pipe(takeUntil(this.destroy$), debounceTime(500))
			.subscribe((result) => {
				this.publisherSelectList = result.resultObj;
				this.isPublisherSelectLoading = false;
			});
	}
	initCategoryVariatonsList(searchKeyWord: string) {
		console.log('GetCategoryVariationByCatgoryId');
		this.isCategoryVariatonsSelectLoading = true;
		this.sellerCategoryService
			.getCategoryVariationByCatgoryIdBySearchKeyWord(searchKeyWord)
			.pipe(takeUntil(this.destroy$), debounceTime(500))
			.subscribe((result) => {
				this.categoryVariationSelectList = result.resultObj;
				this.isCategoryVariatonsSelectLoading = false;
				console.log(this.categoryVariationSelectList);
			});
	}
	onSearchAuthor(value: string) {
		this.initAuthorList(value);
	}
	onSearchPublisher(value: string) {
		this.initPublisherList(value);
	}
	onSearchCategoryVariatons(value: string) {
		this.initCategoryVariatonsList(value);
	}

	getFormData() {
		const val = this.sellerCreateProductForm.value;

		const productUpdateRequest: ProductUpdateRequest = {
			productId: this.productId,
			productName: val.productName,
			categoryId: 1,
			description: val.entireDescription,
			authorId: val.author,
			publisherId: val.publisher,
			countryId: val.country,
			page: val.page,
			publishingYear: val.publishingYear,
			price: val.price,
			originalPrice: val.originalPrice,
			stock: val.stock,
			mass: val.mass,
			length: val.length,
			width: val.width,
			thickness: val.thickness,
			categoryVariations: val.categoryVariaton,
			productImages: this.thumbshandlePreview
		};

		return productUpdateRequest;
	}
	submitForm(): void {
		this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted:true});
		try {
			if (this.getFormData()) {
				this.sellerProductService
					.updateProduct(this.getFormData())
					.pipe(takeUntil(this.destroy$))
					.subscribe(
						(res) => {
							if (res.isSuccessed) {
								if (res.message) {
									this.notificationService.showSuccessNotification(res.message);
								}
							} else {
								if (res.message) {
									this.notificationService.showErrorNotification(res.message);
								}
							}
							this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted:false});
						},
						() => {
							this.notificationService.showErrorNotification('C Li Xy Ra, Vui Lng Th Li');
							this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted:false});
						}
					);
			}
		} catch (error) {
			this.spinner.subjectSubmitLoadingHaveData$.next({isSubmitted:true});
			this.appErrorHandler.handleError(error);
		}
	}

	//Process the images
	drop(event: CdkDragDrop<string[]>) {
		moveItemInArray(this.thumbshandlePreview, event.previousIndex, event.currentIndex);
	}
	removeImageFile(index: number) {
		//console.log(index);
		this.thumbshandlePreview.splice(
			this.thumbshandlePreview.findIndex((x) => x.uid === index.toString()),
			1
		);
	}
	cancel(): void {
		return;
	}

	confirm(index: number): void {
		this.removeImageFile(index);
		this.notificationService.showSuccessNotification(' Xa Thnh Cng');
	}
	beforeUpload = (file: File): boolean => {
		this.fileList = this.fileList.concat(file);
		const formData: FormData = new FormData();
		formData.append('productId', this.productId.toString());
		formData.append('productImage', file, file.name);
		this.sellerProductService
			.postProductImagePreview(formData)
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					console.log(result);
					if (result.resultObj != null) {
						if (result.resultObj.imagePath != null) {
							this.thumbshandlePreview.push({
								uid: (this.uid++).toString(),
								productImageId: null,
								imagePath: result.resultObj?.imagePath
							});
						}
						console.log(this.thumbshandlePreview);
					}
				},
				() => {
					this.notificationService.showErrorNotification('C Li Xy Ra, Vui Lng Th Li');
				}
			);
		return false;
	};
}
