import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {PagedResult} from 'src/app/core/models/common/page/PagedResult';
import {SellerAllProductVM} from 'src/app/core/models/seller/product/seller-all-product.viewmodel';
import {environment} from 'src/environments/environment';

@Component({
	selector: 'app-product-show-table',
	templateUrl: './product-show-table.component.html',
	styleUrls: ['./product-show-table.component.less']
})
export class ProductShowTableComponent implements OnInit {
	@Input() pageResult: PagedResult<SellerAllProductVM>;
	@Input() listOfData: Array<SellerAllProductVM> = [];
	@Output() pageIndex = new EventEmitter<number>();
	@Output() productIdDelete = new EventEmitter<number>();
	@Output() productIdLock = new EventEmitter<number>();
	pageCurrentIndex = 1;
	host: string = environment.apiUrl;

	constructor() {
		this.pageIndex.emit(1);
		this.pageResult = {} as PagedResult<SellerAllProductVM>;
	}
	ngOnInit(): void {}
	confirmDeleteProduct(productId: number) {
		this.productIdDelete.emit(productId);
		//console.log(productId);
	}
	confirmLockProduct(productId: number) {
		this.productIdLock.emit(productId);
		//console.log(productId);
	}
	cancel() {}
	// onScroll() {
	// 	this.pageCurrentIndex = this.pageCurrentIndex + 1;
	// 	this.pageIndex.emit(this.pageCurrentIndex);
	// }
}
