import { SellerUrlAuthor } from './../../../utils/URL/seller-url/seller-author/seller-url-author';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { AuthorResponseVM } from 'src/app/core/models/seller/author/authorResponse.ViewModel';
import { ApiResultWithList } from 'src/app/core/models/common/ApiResultWithList';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class SellerAuthorService {
	languageCode = 'vn';
	categoryId = 1;
  constructor(private http: HttpClient, private sellerUrlAuthorString:SellerUrlAuthor) { }
  getAuthorList(searchKeyWord: string): Observable<ApiResultWithList<AuthorResponseVM>> {
		return this.http.get<ApiResultWithList<AuthorResponseVM>>(
			this.sellerUrlAuthorString.sellerGetAuthorBySearchKeyWordListAPI({
				languageCode: this.languageCode,
				searchKeyWord
			})
		);
	}
}
