import {SellerUrlCategory} from './../../../utils/URL/seller-url/seller-category/seller-order-category';
import {Injectable} from '@angular/core';
import {ApiResultWithList} from 'src/app/core/models/common/ApiResultWithList';
import {CategoryVariationResponseVM} from 'src/app/core/models/seller/category/categoryVariationResponse.ViewModel';
import {Observable} from 'rxjs';
import {HttpClient} from '@angular/common/http';

@Injectable({
	providedIn: 'root'
})
export class SellerCategoryService {
	languageCode = 'vn';
	categoryId = 1;
	constructor(private http: HttpClient, private sellerUrlCategoryString: SellerUrlCategory) {}

  
	getCategoryVariationByCatgoryIdBySearchKeyWord(
		searchKeyWord: string
	): Observable<ApiResultWithList<CategoryVariationResponseVM>> {
		console.log('GetCategoryVariationByCatgoryId');
		return this.http.get<ApiResultWithList<CategoryVariationResponseVM>>(
			this.sellerUrlCategoryString.getCategoryVariationByCatgoryIdBySearchKeyWordAPI(
				{languageCode: this.languageCode, searchKeyWord},
				this.categoryId
			)
		);
	}
}
