import {NotificationModule} from './../modules/notification/notification.module';
/* eslint-disable prefer-arrow/prefer-arrow-functions */
/* eslint-disable @typescript-eslint/naming-convention */
import {EffectsModule} from '@ngrx/effects';
import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {SellerComponent} from './seller.component';
import {SellerBodyComponent} from './components/seller-body/seller-body.component';
import {SellerHeaderComponent} from './components/seller-header/seller-header.component';
import {SellerRoutingModule} from './seller-routing.module';
import {NgZorroAntdModule} from 'src/app/core/utils/ng-zorro-antd/ng-zorro-antd.module';
import {IconsProviderModule} from 'src/app/icons-provider.module';
import {NzLayoutModule} from 'ng-zorro-antd/layout';
import {NzMenuModule} from 'ng-zorro-antd/menu';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {HttpClient, HttpClientModule} from '@angular/common/http';
import {MaterialModule} from 'src/app/core/utils/material.module';
import {TranslateLoader, TranslateModule} from '@ngx-translate/core';
import {TranslateHttpLoader} from '@ngx-translate/http-loader';
import {HomeComponent} from './components/seller-body/home/home.component';
import {SellerLoginModule} from './module/seller-login/seller-login.module';
import {LoginEffects} from 'src/app/core/store/seller/login/login.effects';
import {ProductComponent} from './components/seller-body/product/product.component';
import {SellerSidebarComponent} from './components/seller-sidebar/seller-sidebar.component';
import {ProductCreateComponent} from './components/seller-body/product/product-create/product-create.component';
import {ProductAllComponent} from './components/seller-body/product/product-all/product-all.component';
import {SellerSettingsEffects} from 'src/app/core/store/seller/setting/seller-settings.effects';
import {InfiniteScrollModule} from 'ngx-infinite-scroll';
import {CKEditorModule} from '@ckeditor/ckeditor5-angular';
import {ProductUpdateComponent} from './components/seller-body/product/product-update/product-update.component';
import {SellerNotificatioHeaderComponent} from './components/seller-header/seller-notificatio-header/seller-notification-header.component';
import {ProductShowTableComponent} from './components/seller-body/product/components/product-show-table/product-show-table.component';
import {ProductUncomfirmTableComponent} from './components/seller-body/product/components/product-uncomfirm-table/product-uncomfirm-table.component';
import { StatisticModule } from '../modules/statistic/statistic.module';

@NgModule({
	declarations: [
		SellerComponent,
		SellerBodyComponent,
		SellerHeaderComponent,
		HomeComponent,
		ProductComponent,
		SellerSidebarComponent,
		ProductCreateComponent,
		ProductAllComponent,
		ProductUpdateComponent,
		SellerNotificatioHeaderComponent,
		ProductShowTableComponent,

		ProductUncomfirmTableComponent
	],
	imports: [
		CommonModule,
		ReactiveFormsModule,
		FormsModule,
		NgZorroAntdModule,
		SellerRoutingModule,
		IconsProviderModule,
		NzLayoutModule,
		NzMenuModule,
		SellerLoginModule,
		HttpClientModule,
		MaterialModule,
		CKEditorModule,
		StatisticModule,
		NotificationModule,
		TranslateModule.forRoot({
			loader: {
				provide: TranslateLoader,
				useFactory: HttpLoaderFactory,
				deps: [HttpClient]
			}
		}),

		//StoreModule.forFeature("feature_core", reducers, {metaReducers}),

		EffectsModule.forFeature([LoginEffects]),
		EffectsModule.forFeature([SellerSettingsEffects]),

		// StoreModule.forFeature('sellersettings', sellerSettingReducer),
		// StoreModule.forFeature('sellerLogin', loginReducer),
		InfiniteScrollModule

		//StoreModule.forFeature("feature_SellerLogin", loginReducer), //ddky  to feature mi
		// StoreModule.forFeature("feature_SellerSettingsas", sellerSettingReducer),
	],
	bootstrap: [SellerComponent]
})
export class SellerModule {}
export function HttpLoaderFactory(http: HttpClient) {
	return new TranslateHttpLoader(http);
}
