import {Component, ViewEncapsulation, OnInit} from '@angular/core';
import {takeUntil} from 'rxjs/internal/operators/takeUntil';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {SpinnerService} from 'src/app/core/services/comon-services/spinner/spinner.service';

@Component({
	selector: 'app-seller',
	templateUrl: './seller.component.html',
	styleUrls: ['./seller.component.less'],
	encapsulation: ViewEncapsulation.None,
	providers: [DestroyService]
})
export class SellerComponent implements OnInit {
	isSubmited: boolean;
	constructor(private sellerSpinnerService: SpinnerService, private destroy$: DestroyService) {
		this.isSubmited = false;
	}
	ngOnInit(): void {
		this.controlSpinner();
	}
	
	controlSpinner() {
		this.sellerSpinnerService.subjectSubmitLoadingHaveData$.pipe(takeUntil(this.destroy$)).subscribe((result) => {
			this.isSubmited = result.isSubmitted;
			console.log(this.isSubmited);
		});
	}
}
