import {SellerUrlPublisher} from './../../../utils/URL/seller-url/seller-publisher/seller-url-publisher';
import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {ApiResultWithList} from 'src/app/core/models/common/ApiResultWithList';
import {PublisherResponseVM} from 'src/app/core/models/seller/publisher/publisher.ViewModel';
import {Observable} from 'rxjs';

@Injectable({
	providedIn: 'root'
})
export class SellerPublisherService {
	languageCode = 'vn';
	categoryId = 1;
	constructor(private http: HttpClient, private sellerUrlPublisherString: SellerUrlPublisher) {}
	getPublisherList(searchKeyWord: string): Observable<ApiResultWithList<PublisherResponseVM>> {
		return this.http.get<ApiResultWithList<PublisherResponseVM>>(
			this.sellerUrlPublisherString.sellerGetPublisherBySearchKeyWordListAPI({
				languageCode: this.languageCode,
				searchKeyWord
			})
		);
	}
}
