import { SellerUrlBase } from './../../../utils/URL/seller-url/seller-url-base';
import {ApiResult} from '../../../models/common/ApiResult';
import {Observable} from 'rxjs';
import {HttpClient, HttpHeaders, HttpResponse} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {map} from 'rxjs/operators';
import {Seller} from 'src/app/core/models/seller/seller.model';
@Injectable({
	providedIn: 'root'
})
export class SellerAccountService {
	public user: Observable<ApiResult<Seller>>;
	constructor(private http: HttpClient, private urlString: SellerUrlBase) {}
	login(email: string, passWord: string, remember: boolean, urlReturn: string): Observable<ApiResult<Seller>> {
		const httpOptions = {
			// eslint-disable-next-line @typescript-eslint/naming-convention
			headers: new HttpHeaders({'Content-Type': 'application/json'}),
			withCredentials: true,
			//this is required so that Angular returns the Cookies received from the server. The server sends cookies in Set-Cookie header. Without this, Angular will ignore the Set-Cookie header
			observe: 'response' as 'response'
		};
		return this.http
			.post<ApiResult<Seller>>(
				this.urlString.sellerUrlLogin,
				{
					email,
					passWord,
					remember,
					urlReturn
				},
				httpOptions
			)
			.pipe(
				//shareReplay(3),
				map(
					(result: HttpResponse<ApiResult<Seller>>) => {
						if (result.body.isSuccessed === true) {
							if (result.body.resultObj.tokenValue) {
								// this.appCookieService.set("seller-token", result.body.resultObj.tokenValue);
								return result.body;
							} else {
								return result.body;
							}
						} else {
							return result.body;
						}
					},
					(error: Error) => {
						console.log('seller servce error: ' + error);
						return error;
					}
				)
			);
	}
}
