import {Component, OnInit} from '@angular/core';
import {NzDrawerPlacement} from 'ng-zorro-antd/drawer';
import {Store} from '@ngrx/store';
import {SellerAppState} from 'src/app/core/store/seller/seller-app.state';
import {
	actionSettingsChangeLanguage,
	actionSettingsChangeTheme
} from 'src/app/core/store/seller/setting/seller-setting.actions';
import {Language, Theme} from 'src/app/core/store/seller/setting/seller-settings.state';
import {selectSettingsLanguage, selectTheme} from 'src/app/core/store/seller/setting/seller-settings.selectors';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {takeUntil} from 'rxjs/internal/operators/takeUntil';
import {loginReset} from 'src/app/core/store/seller/login/login.actions';
import {LocalStorageService} from 'src/app/core/services/comon-services/local-storage-service/localstorage.service';
import {Router} from '@angular/router';
import {AuthService} from 'src/app/core/services/auth/auth.service';

@Component({
	selector: 'app-seller-header',
	templateUrl: './seller-header.component.html',
	styleUrls: ['./seller-header.component.less'],
	providers: [DestroyService]
})
export class SellerHeaderComponent implements OnInit {
	visible = false;
	themes = [
		{value: 'dark', label: 'Dark|Ti'},
		{value: 'default', label: 'light|sng'}
	];
	languages = [
		{value: 'seller-en', label: 'EN'},
		{value: 'seller-vn', label: 'VN'}
	];
	placement: NzDrawerPlacement = 'left';
	themeDefault: Theme;
	languageDefault: Language;
	sellerInforDisplay: any;
	isLogin?: boolean;
	constructor(
		private destroy$: DestroyService,
		private store: Store<SellerAppState>, //private activatedRoute: ActivatedRoute,
		private localStore: LocalStorageService,
		private router: Router,
		private authService: AuthService
	) {
		this.themeDefault = 'default';
		this.languageDefault = 'seller-vn';
		this.isLogin = false;
	}
	ngOnInit(): void {
		this.checkLogin();
		this.store
			.select(selectTheme)
			.pipe(takeUntil(this.destroy$))
			.subscribe((result) => {
				this.themeDefault = result;
			});
		this.store
			.select(selectSettingsLanguage)
			.pipe(takeUntil(this.destroy$))
			.subscribe((result) => {
				this.languageDefault = result != null ? result : 'seller-vn';
			});
	}
	onThemeSelect(theme: Theme): void {
		this.store.dispatch(actionSettingsChangeTheme({theme}));
	}
	onLanguageSelect(language: Language) {
		this.store.dispatch(actionSettingsChangeLanguage({language}));
	}

	open(): void {
		this.visible = !this.visible;
	}
	close(): void {
		this.visible = false;
	}
	checkLogin() {
		if (this.authService.isAuthenticated('seller-token')) {
			this.isLogin = true;
		} else {
			this.isLogin = false;
			//this.appCookieService.remove('customer-token');
			this.localStore.removeItem('seller-token');
			this.store.dispatch(loginReset());
			console.log('customerAuthGuard: false');
		}
	}
	cancel() {
		return;
	}
	confirm() {
		this.logout();
	}
	logout() {
		this.store.dispatch(loginReset());
		this.localStore.removeItem('seller-token');
		this.router.navigate(['/seller/login'], {queryParams: {returnUrl: this.router.routerState.snapshot.url}});
	}
}
