import {Observable} from 'rxjs';
import {HttpClient} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {ApiResult} from 'src/app/core/models/common/ApiResult';
import {PagedResult} from 'src/app/core/models/common/page/PagedResult';
import {RequestBase} from 'src/app/core/models/common/request/RequestBase';
import {BookDetailRespond} from 'src/app/core/models/product/respond/BookDetailRespond';
import {ImageUploadPreviewResponse} from 'src/app/core/models/product/respond/ImageUploadPreviewResponse.viewmodel';
import {ProductUpdateRequest} from 'src/app/core/models/seller/product/request/ProductUpdateRequest';
import {SellerAllProductVM} from 'src/app/core/models/seller/product/seller-all-product.viewmodel';
import {SellerUrlProduct} from 'src/app/core/utils/URL/seller-url/seller-product/seller-url-product';

@Injectable({
	providedIn: 'root'
})
export class SellerProductService {
	languageCode = 'vn';
	categoryId = 1;
	constructor(private sellerUrlProductString: SellerUrlProduct, private http: HttpClient) {}
	getAllProductPaging(pageIndex: number, typeProduct: number) {
		return this.http.get<ApiResult<PagedResult<SellerAllProductVM>>>(
			this.sellerUrlProductString.sellerGetProductsAPI(
				{
					pageIndex,
					pageSize: 10,
					languageCode: this.languageCode
					//token: this.token
				},
				typeProduct
			)
		);
	}
	createProduct(newProduct: FormData): Observable<ApiResult<number>> {
		newProduct.append('languageCode', this.languageCode);
		newProduct.append('categoryId', String(this.categoryId));
		return this.http.post<ApiResult<number>>(this.sellerUrlProductString.sellerPostCreateProduct(), newProduct);
	}
	postProductImagePreview(newProductImage: FormData) {
		return this.http.post<ApiResult<ImageUploadPreviewResponse>>(
			this.sellerUrlProductString.sellerPostProductImagePreviewAPI(),
			newProductImage
		);
	}
	updateProduct(productUpate: ProductUpdateRequest) {
		productUpate.languageCode = this.languageCode;
		return this.http.post<ApiResult<boolean>>(this.sellerUrlProductString.sellerUpdateProductAPI(), productUpate);
	}
	getProductById(requestBase: RequestBase) {
		requestBase.languageCode = this.languageCode;
		return this.http.get<ApiResult<BookDetailRespond>>(
			this.sellerUrlProductString.sellerGetProductDetailByIdAPI(requestBase)
		);
	}
	lockProduct(productId: number) {
		return this.http.put<ApiResult<boolean>>(this.sellerUrlProductString.sellerLockProduct(), productId);
	}
	deleteProduct(productId: number) {
		return this.http.delete<ApiResult<boolean>>(this.sellerUrlProductString.sellerDeleteProduct(productId));
	}
}
