import { createFeatureSelector, createSelector } from '@ngrx/store';
import { SellerAppState } from '../seller-app.state';
import { LoginState } from './login.state';
//const featureSellerLogin= createFeatureSelector<LoginState>('feature_SellerLogin');
//const featureCore= createFeatureSelector<AppState>('feature_core');
export const selectLoginState = createFeatureSelector<SellerAppState, LoginState>('sellerLogin');
export const loginStateSelector= createSelector(selectLoginState, state=>state);
export const loginUserSelector= createSelector(selectLoginState, state=>state.user);
export const loginStatusSelector= createSelector(selectLoginState, state=>state.status);
export const loginMessageSelector= createSelector(selectLoginState, state=>state.message);
export const loginIsAuthenticatedSelector= createSelector(selectLoginState, state=>state.isAuthenticated);
