import {Component, Input, OnInit} from '@angular/core';
import {takeUntil} from 'rxjs/internal/operators/takeUntil';
import {AppErrorHandler} from 'src/app/core/error-handler/app-error-handler.service';
import {NotificationResponse} from 'src/app/core/models/common/notification/response/notification-response.viewmodel';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import { SellerNotificationService } from 'src/app/core/services/seller/seller-notification/seller-notification.service';
import { SellerSignalService } from 'src/app/core/services/seller/seller-notification/hubs/seller-signal.service';

@Component({
	selector: 'app-seller-notification-header',
	templateUrl: './seller-notification-header.component.html',
	styleUrls: ['./seller-notification-header.component.less']
})
export class SellerNotificatioHeaderComponent implements OnInit {
	@Input() fontSize: number = 24;
	notificationCount: number;
	notificationList: Array<NotificationResponse> = [];
	isNotificationVisible: boolean = false;
	isNotificationListVisible: boolean = false;
	pageIndex: number = 1;
	isLoading: boolean = true;
	constructor(
		private notificationService: SellerNotificationService,
		private signalService: SellerSignalService,
		private destroy$: DestroyService,
		private appErrorHandler: AppErrorHandler
	) {
		this.notificationCount = 0;
	}
	ngOnInit(): void {
		//console.log(this.fontSize);
		this.getNotificationCount();
		this.signalService.signalNotificationCount.pipe(takeUntil(this.destroy$)).subscribe((result) => {
			this.notificationCount = result;
			//console.log(this.notificationCount);
		});
	}
	getNotificationCount() {
		this.notificationService
			.getNotificationCount()
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					if (result) {
						console.log(result);
						if (result.isSuccessed) {
							if (result.resultObj) {
								this.notificationCount = result.resultObj.count;
							} else {
							}
							console.log(this.notificationCount);
						} else {
						}
					} else {
					}
				},
				(error) => {
					this.appErrorHandler.handleErrorWithNoti(error);
				}
			);
	}
	changeShowNotificationListStatus(value: boolean): void {
		this.isNotificationListVisible = value;
		console.log(value);
		if (this.isNotificationListVisible) {
			this.pageIndex = 1;
			this.notificationList = [];
			console.log('value');
			this.getNotificationList(this.pageIndex);
		} else {
			let nofiticationHasReadList: Array<number> = [];
			this.notificationList.forEach((value) => {
				if (value.hasRead == false) {
					nofiticationHasReadList.push(value.notificationId);
				}
			});

			console.log(nofiticationHasReadList.length);
			if (nofiticationHasReadList.length > 0) {
				console.log(nofiticationHasReadList);
				this.notificationService.postNotificationHasRead(nofiticationHasReadList).subscribe();
			}
		}
	}
	pageNotificationScroll(value: number) {
		this.pageIndex = value;
		console.log(value);
		if (this.isNotificationListVisible && this.pageIndex > 1) {
			console.log('value');
			this.getNotificationList(value);
		} else {
		}
	}
	getNotificationList(pageIndex: number) {
		this.isLoading = true;
		this.notificationService
			.getNotificationList({pageIndex: pageIndex, pageSize: 10})
			.pipe(takeUntil(this.destroy$))
			.subscribe(
				(result) => {
					if (result) {
						console.log(result);
						if (result.isSuccessed) {
							if (result.resultObj?.items) {
								Array.prototype.push.apply(this.notificationList, result.resultObj?.items);
								this.notificationList;
								this.isLoading = false;
							}
						}
					} else {
					}
				},
				(error) => {
					this.appErrorHandler.handleErrorWithNoti(error);
				}
			);
	}
}
