import { Component, OnInit } from '@angular/core';
import { StatisticResponse } from 'src/app/core/models/common/statistic/response/statistic-response.viewmodel';
import { SellerStatisticService } from 'src/app/core/services/seller/seller-statistic/seller-statistic.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.less']
})
export class HomeComponent implements OnInit {
  statisticItems: Array<StatisticResponse> = [];

	constructor(private sellerStatisticService: SellerStatisticService) {}

	ngOnInit(): void {
		this.getStatistics();
	}
	getStatistics() {
		this.sellerStatisticService.getStatistics().subscribe((result) => {
			if (result) {
				if (result.isSuccessed) {
					if (result.resultObj) {
						this.statisticItems = result.resultObj;
						console.log(this.statisticItems);
					}
				} else {
				}
			} else {
			}
		});
	}
}
